import React, { useState, useEffect } from "react";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faPhone,
  faHome,
  faClock,
  faCalendarAlt,
  faUsers,
  faBook,
} from "@fortawesome/free-solid-svg-icons";

import Button from "components/Button";
import Card from "components/Card";
// import Nav from "components/Nav";

import "./App.css";
import logo from "img/logo.png";

//<p className="px-1 leading-snug tracking-wider text-white text-center text-4xl font-extrabold w-screen pt-3 pb-5 bg-black bg-opacity-10 backdrop-filter backdrop-blur-sm backdrop-opacity-80">
//         {/* TODO: replace (dream home) with word flipper */}
//       Buy your dream home with confidence.
//   </p>

export default function App() {
  const handleScheduleClick = () => {
    console.log("scheduling!");
  };

  const handleSampleReportClick = () => {
    console.log("Sample report!");
  };

  const handleSubmitForm = (e) => {
    e.preventDefault();
    console.log("form submitted");
  };

  // Word flipper logic
  const phrases = [
    "first home",
    "next home",
    "dream home",
    "new office",
    "mountain cabin",
    "lakehouse",
  ];
  const [index, setIndex] = useState(0);

  useEffect(() => {
    const interval = setInterval(() => {
      // update current word here
      setIndex((index) => index + 1);
    }, 3500); // every 3 seconds

    return () => clearInterval(interval);
  }, []);

  return (
    <div className="bg-ciWhite">
      {/* HEADER */}
      <div className="relative flex flex-col justify-center z-30 items-center h-big bg-houseSm md:bg-houseLg bg-cover md:bg-center">
        <img src={logo} alt="logo" className="w-32 mt-4" />
        {/* <div className='bg-logo w-32 h-32 z-50'></div> */}

        <div className="w-screen pt-3 pb-5 px-1 text-shadow leading-snug tracking-wider text-ciWhite text-center text-4xl md:text-5xl lg:text-7xl max-w-7xl font-black">
          <p>Buy your</p>{" "}
          <p className="text-ciGreen">{phrases[index % phrases.length]}</p>{" "}
          <p>with confidence</p>
        </div>

        <div className="absolute -bottom-8">
          <Button
            onClick={handleScheduleClick}
            icon="faCalendarAlt"
            text="Schedule Your Inspection"
          ></Button>
        </div>
      </div>

      {/* WHY US */}
      <div className="my-20">
        <p className="leading-snug tracking-wider text-center text-4xl font-extrabold">
          Why Core Inspection?
        </p>

        {/* TODO: set max widths of cards, make a grid of 2x2 at md, row of 4 at lg */}
        <div className="flex flex-col lg:flex-row items-center justify-center">
          <Card
            backgroundImage="bg-sampleReport"
            text="View a Sample Report"
            alt="Sample report"
            onClick={handleSampleReportClick}
          ></Card>
          <Card
            backgroundImage="bg-drone"
            text="Modern Inspection"
            alt="Modern inspection"
            onClick={handleSampleReportClick}
          ></Card>
          <Card
            backgroundImage="bg-oneFifty"
            text="150-Point Inspection"
            alt="150-point inspection"
            onClick={handleSampleReportClick}
          ></Card>
          <Card
            backgroundImage="bg-certified"
            text="Insured & InterNACHI Certified"
            alt="Insured and InterNACHI certified"
            onClick={handleSampleReportClick}
          ></Card>
        </div>
      </div>

      {/* SERVICES */}
      <div className="bg-ciBlue pt-5 pb-14 flex flex-col items-center">
        <p className="leading-snug tracking-wider text-center text-4xl text-white font-extrabold">
          Services
        </p>

        {/* TODO: sizing, alignment, phone number & icon */}
        <p className="text-center text-white">
          Don't see a time that works for you?
        </p>
        <div className="flex items-center space-x-2 text-white pb-3">
          <p>Give us a call at</p>
          <FontAwesomeIcon icon={faPhone} />
          <p>(706) 206-5512</p>
        </div>

        {/* TODO: set max widths of cards, make a grid of 2x2 */}
        <div className="flex flex-col lg:flex-row items-center justify-center w-full">
          <Card
            backgroundImage="bg-residential"
            text="Book Residential"
            alt="Residential booking"
            onClick={handleSampleReportClick}
          ></Card>
          <Card
            backgroundImage="bg-commercial"
            text="Book Commercial"
            alt="Commercial booking"
            onClick={handleSampleReportClick}
          ></Card>
        </div>
      </div>

      {/* PROCESS */}
      <div className="mt-14 px-2">
        <p className="mb-5 leading-snug tracking-wider text-center text-4xl font-extrabold">
          The Process
        </p>

        <div className="flex flex-col items-center space-y-10">
          <div className="flex flex-col items-center space-y-3 text-center">
            <div className="w-20 h-20 border-black border-4 font-bold text-2xl rounded-full flex justify-center items-center">
              <FontAwesomeIcon icon={faCalendarAlt} size="lg" />
              {/* TODO: add calendar modal here */}
            </div>
            <Button
              onClick={handleScheduleClick}
              text="Schedule Your Inspection"
            ></Button>
          </div>

          <div className="flex flex-col items-center space-y-3 text-center">
            <div className="w-20 h-20 border-black border-4 font-bold text-2xl rounded-full flex justify-center items-center">
              <FontAwesomeIcon icon={faUsers} size="lg" />
            </div>
            <p className="text-xl">Our team goes to work</p>
          </div>

          <div className="flex flex-col items-center space-y-3 px text-center">
            <div className="w-20 h-20 border-black border-4 font-bold text-2xl rounded-full flex justify-center items-center">
              <FontAwesomeIcon icon={faBook} size="lg" />
            </div>
            {/* TODO: link to sample binder / report */}
            <p className="text-xl">Your inspection binder is yours forever</p>
          </div>
        </div>
      </div>

      {/* TESTIMONIALS */}
      {/* TODO: reviews from Google, HomeAdvisor, etc */}

      {/* CONTACT FORM */}
      <div className="mt-14 px-2 text-center max-w-7xl m-auto">
        <p className="leading-snug tracking-wider text-center text-4xl font-extrabold">
          Get in Touch
        </p>

        {/* TODO: sizing, alignment, & icon */}
        <p>We'll reply within a business day.</p>

        <form
          name="contact"
          method="POST"
          data-netlify="true"
          onSubmit={handleSubmitForm}
          className="flex flex-col items-center space-y-4 bg-ciBlue my-3 py-5 rounded-md"
        >
          <p>
            <label className="font-bold flex flex-col text-ciWhite">
              Your Name{" "}
              <input
                className="w-60 p-2 text-center text-black rounded-sm"
                type="text"
                name="name"
              />
            </label>
          </p>
          <p>
            <label className="font-bold flex flex-col text-ciWhite">
              Your Email{" "}
              <input
                className="w-60 p-2 text-center text-black rounded-sm"
                type="email"
                name="email"
              />
            </label>
          </p>
          <p>
            <label className="font-bold flex flex-col items-center text-ciWhite">
              Message{" "}
              <textarea
                className="w-60 md:w-96 h-16 p-1 text-black rounded-sm"
                name="message"
              ></textarea>
            </label>
          </p>
          <p>
            <button
              className="font-bold bg-white px-10 py-2 rounded-sm"
              type="submit"
            >
              Send
            </button>
          </p>
        </form>
      </div>

      {/* TODO: add facebook link & icon */}
      {/* Footer */}
      <div className="w-full bg-ciCharcoal text-ciWhite px-4 pt-10 pb-4 flex flex-col md:flex-row md:flex-wrap space-y-5 justify-center">
        {/* spiel & CTA */}
        <div className="flex flex-col items-center text-center md:w-3/5 md:justify-center md:pl-3">
          {/* TODO: serving which areas, little sales pitch */}
          <p className="leading-snug tracking-wider text-2xl font-extrabold">
            Proudly serving yous
          </p>
          <p>
            For seriously. We'll inspect, we'll look at items, look into
            activity spaces, and even go as far as to do so.
          </p>

          <div className="bg-vetOwned h-28 w-28 mt-5 bg-contain bg-center bg-no-repeat"></div>
        </div>

        {/* contact & business info */}
        <div className="flex flex-col pb-2 md:w-2/5 md:justify-center items-center space-y-2">
          <p className="leading-snug tracking-wider text-2xl font-extrabold">
            Get in touch
          </p>
          <div className="flex items-center space-x-2">
            <FontAwesomeIcon icon={faPhone} />
            <p>(706) 206-5512</p>
          </div>
          <div className="flex items-center space-x-2">
            <FontAwesomeIcon icon={faHome} />
            <p>P.O. Box 492 Crawford, GA 30630</p>
          </div>
          <div className="flex flex-col">
            <div className="flex items-center space-x-2">
              <FontAwesomeIcon icon={faClock} />
              <p>8am - 6pm</p>
            </div>
            <p>Monday - Friday</p>
          </div>
        </div>

        {/* Copyright shit */}
        <div className="text-center text-xs pb-2">
          <p>© Copyright Core Inspection, LLC, 2021</p>
        </div>
      </div>
    </div>
  );
}
